<template>
  <div class="wrap-draft-list">
         <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>草稿列表</el-breadcrumb-item>
      </el-breadcrumb>
    </div> 
    <div class="content">
        <el-table
        :data="draftList"
        style="width: 100%" class="report">
            <el-table-column prop="draft_id" label="草稿编码"></el-table-column>
            <el-table-column prop="title" label="商品标题"></el-table-column>
            <el-table-column prop="add_time" label="更新日期"></el-table-column>
            <el-table-column p label="操作">
                <template slot-scope="scope">
                    <el-button type="primary" @click="edit(scope.row)" size="mini">编辑</el-button>
                    <el-button type="danger" @click="delet(scope.row)" size="mini">删除</el-button>
                </template>
            </el-table-column>
        
        </el-table>
    </div>
  </div>
</template>
<script>
export default {
  data(){
      return{
          draftList:[]
      }
    
  },
  methods:{
      getDraftList(){
          this.axios.post('/proxy/fv/supplier/goods.php?act=getDraftList',{body:{page:1,pageSize:1000}}).then(res=>{
              let data=res.data;
              if(data.head.code=='10000'){
                  this.draftList=data.body.rows;
              }
          })
      },
      edit(data){
          this.$router.push('/vendor/goods-add/draft?id='+data.draft_id+'&cate='+data.cat_id)
      },
      delet(data){
          console.log(1111111)
          this.$confirm('您确定要删除该草稿？','提示',{
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(()=>{
            this.axios.post('/proxy/fv/supplier/goods.php?act=delDrafts',{body:{d_id:data.draft_id}}).then(res=>{
                let data=res.data;
                if(data.head.code=='10000'){
                    this.getDraftList();
                }
            })              
          }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
        });
      }
  },
  created(){
      this.getDraftList();
  }
}
</script>
<style lang="less" scoped>

</style>





